.card-large {
    position: relative;
    padding: 0.5%;
}

.card-large a > img {
    object-fit: none;
    max-width: 100%;  
    border-style: solid;
    border-width: 5px;
    border-color: var(--darkpurple);
    background-color: var(--white); 
}

.card-large:hover p {
    visibility: visible;
}

.card-large > p {
    position: absolute; 
    bottom: 8px;
    left: 4px;     
    visibility: hidden;
    max-width: fit-content;
    padding: 3%;
    border-style: solid;
    border-width: 1px;
    border-radius: 10px;
    background-color: var(--darkpurple);
    border-color: var(--darkpurple);  
}

.card-small-flexbox {
    display: flex;
    flex-wrap: wrap;
    justify-content: space-between;
}

.card-small {
    flex: 40%;     
    text-align: justify;   
}

.card-small p {
    position: relative;
    bottom: 30px;
    left: 0px;
    visibility: hidden;
    align-self: flex-start;      
    max-width: fit-content;    
    padding: 1%;
    border-style: solid;
    border-width: 1px;
    border-color: var(--darkpurple); 
    background-color: var(--darkpurple); 
}

.card-small img {  
    object-fit: none;
    display: block;     
    max-width: 90%;
    margin-top: 2%;
    border-style: solid;
    border-width: 5px;
    border-color: var(--darkpurple); 
}

.card-small:hover p{
    visibility: visible;
}
